import { Component, OnInit } from '@angular/core';
import { PageValue } from '../../core/models/page-value.model';

import { ContentService } from '../../core/services/content.service';
import { ActivatedRoute, Router } from '@angular/router';
import { ContentModel } from '../../core/models/content.model';

import * as _moment from 'moment';
const moment = _moment;

@Component({
  selector: 'app-multiple-detail',
  templateUrl: './multiple-detail.component.html',
  styleUrls: ['./multiple-detail.component.scss']
})
export class MultipleDetailComponent implements OnInit {

  dataSource = []
  isLoadingResults = false;
  pageValue = new PageValue();
  pageSize = 10;

  contentObj = new ContentModel();
  listStory = [];
  lang = '';

  constructor(
    private router: Router,
    private activatedRoute: ActivatedRoute,
    private contentService: ContentService
  ) { }

  ngOnInit() {
    this.activatedRoute.params.subscribe(params => {
      this.lang = params.lang
      this.getContentMulti(params.id, params.lang, 'story');
    });
  }

  getContentMulti(contentId, lang, type) {
    this.contentService.getContents(false, contentId, lang, type).subscribe(
      response => {
        if (response.status) {
          let listMulti = []
          let langs = lang;
          if (langs == 'cn' || langs == 'CN') {
            langs = 'zh-cn'
          }
          response.objectValue.map((item, i) => {
            listMulti.push({
              ...item,
              imageUrl: item.images[0].imageUrl,
              create_date: moment(item.createDate).locale(langs).format('MMM D').toString(),
              openTime: this.displayOpenTime(item.businessHours)
            });
          })
          this.listStory = listMulti;
        }
      }
    )
  }

  onView(id: string, lang: string) {
    this.router.navigate(['/content', id, lang.toUpperCase()], { relativeTo: this.activatedRoute });
  }

  displayOpenTime(businessHour): string {
    if (!businessHour) {
      return;
    }
    const sorter = {
      // "sunday": 0, // << if sunday is first day of week
      'mon': 1,
      'tue': 2,
      'wed': 3,
      'thu': 4,
      'fri': 5,
      'sat': 6,
      'sun': 7
    };
    businessHour.sort(function sortByDay(a, b) {
      let day1 = a.day.toLowerCase();
      let day2 = b.day.toLowerCase();

      if (sorter[day1] != sorter[day2]) {
        return sorter[day1] - sorter[day2];
      }

      a.openTime.replace(':', '.');
      b.openTime.replace(':', '.');
      return parseFloat(a.openTime) - parseFloat(b.openTime);
    });

    let fullString = [];
    businessHour.forEach(data => {
      const mergeString = data.day + ' (' + data.openTime + ' - ' + (data.closeTime) + ')';
      fullString.push(mergeString);
    });

    return fullString.join('<br>');
  }

  onDownload() {
    var userAgent = navigator.userAgent || navigator.vendor;
    if (/android/i.test(userAgent)) {
      window.location.href = 'https://play.google.com/store/apps/details?id=com.thaidigitalplatform.tagthai&hl=en';
    }
    else if (/iPad|iPhone|iPod/.test(userAgent)) {
      window.location.href = 'https://apps.apple.com/th/app/tagthai/id1457643150';
    }
    else {
      window.location.href = "http://www.tagthai.com/en/home";
    }
  }

}
