import { Component, OnInit } from '@angular/core';
import { ContentService } from '../../core/services/content.service';
import { ActivatedRoute } from '@angular/router';
import { TitleTagService } from '../../core/services/titleTagService .service'
import { from } from 'rxjs';


@Component({
  selector: 'app-single-detail',
  templateUrl: './single-detail.component.html',
  styleUrls: ['./single-detail.component.scss']
})
export class SingleDetailComponent implements OnInit {

  slugList: any = []
  businessHour: any
  imageList: any = []
  title: string
  address: string
  price: string
  body: string

  constructor(
    private activatedRoute: ActivatedRoute,
    private contentService: ContentService,
    private titleTagService: TitleTagService
  ) { }


  ngOnInit() {
    this.activatedRoute.params.subscribe(params => {
      this.getContentSingle(params.id, params.lang, 'content');
    });
  }

  getContentSingle(contentId, lang, type) {
    this.contentService.getContents(false, contentId, lang, type).subscribe(
      response => {
        let data = response.objectValue[0]
        this.titleTagService.setSocialMediaTags(data.title, data.body, data.images[0].imageUrl);
        this.slugList = data.slugs
        this.businessHour = data.businessHours
        this.imageList = data.images
        this.title = data.title
        this.body = data.body
        this.address = data.location.name
        this.price = `${data.price}  ${data.unit}`
      }
    )
  }

  displayOpenTime(businessHour): string {
    if (!businessHour) {
      return;
    }
    const sorter = {
      // "sunday": 0, // << if sunday is first day of week
      'mon': 1,
      'tue': 2,
      'wed': 3,
      'thu': 4,
      'fri': 5,
      'sat': 6,
      'sun': 7
    };
    businessHour.sort(function sortByDay(a, b) {
      let day1 = a.day.toLowerCase();
      let day2 = b.day.toLowerCase();

      if (sorter[day1] != sorter[day2]) {
        return sorter[day1] - sorter[day2];
      }

      a.openTime.replace(':', '.');
      b.openTime.replace(':', '.');
      return parseFloat(a.openTime) - parseFloat(b.openTime);
    });

    let fullString = [];
    businessHour.forEach(data => {
      const mergeString = data.day + ' (' + data.openTime + ' - ' + (data.closeTime) + ')';
      fullString.push(mergeString);
    });

    return fullString.join('<br>');
  }

  onDownload() {
    var userAgent = navigator.userAgent || navigator.vendor;
    if (/android/i.test(userAgent)) {
      window.location.href = 'https://play.google.com/store/apps/details?id=com.thaidigitalplatform.tagthai&hl=en';
    }
    else if (/iPad|iPhone|iPod/.test(userAgent)) {
      window.location.href = 'https://apps.apple.com/th/app/tagthai/id1457643150';
    }
    else {
      window.location.href = "http://www.tagthai.com/en/home";
    }
  }

}