import { Injectable } from '@angular/core';
// import { Http, Headers, RequestOptions, Response } from '@angular/http';
import { HttpClient, HttpErrorResponse, HttpHeaders } from '@angular/common/http';
import { Observable } from 'rxjs';
import { TokenManagementService } from '../token-management.service';
import { PageValue } from '../../models/page-value.model';

@Injectable()
export class HttpClientService {

    constructor(public tokenManagementService: TokenManagementService,
        private http: HttpClient
    ) {
    }

    public get(url: string) {
        let headers = new HttpHeaders();
        headers.append('Content-Type', 'application/json');
        this.tokenManagementService.getFullToken().subscribe(res => {
            if (res) {
                return this.http.get(url, { headers: headers });
            }
        });
    }

    public post(url: string, requestValue) {
        let headers = new HttpHeaders();
        headers.append('Content-Type', 'application/json');
        this.tokenManagementService.getFullToken().subscribe(res => {
            if (res) {
                return this.http.post(url, requestValue, { headers: headers });
            }
        });
    }

    public put(url: string, requestValue) {
        let headers = new HttpHeaders();
        headers.append('Content-Type', 'application/json');
        return this.tokenManagementService.getFullToken().subscribe(res => {
            if (res) {
                return this.http.put(url, requestValue, { headers: headers });
            }
        });
    }

    public delete(url: string) {
        let headers = new HttpHeaders();
        headers.append('Content-Type', 'application/json');
        return this.tokenManagementService.getFullToken().subscribe(res => {
            if (res) {
                return this.http.delete(url, { headers: headers });
            }
        });
    }



    public handleError(error: HttpErrorResponse | any) {
        let errMsg: string;
        if (error instanceof HttpErrorResponse) {

            alert(error.message)
            // const body = error.json() || '';
            // const err = error || JSON.stringify(error);
            // errMsg = `${err}`;
            errMsg = error.message ? error.message : error.toString();
        } else {
            errMsg = error.message ? error.message : error.toString();
        }
        console.error(errMsg);
        return Observable.throw(errMsg);
    }

    setRequestValue(_action: string, _requestObject: any) {
        let requestValue = {
            action: _action,
            requestObject: _requestObject
        }
        return requestValue;
    }

    setRequest(_action: string, _requestObject: any, _pageValue: any) {
        let requestValue = {
            action: _action,
            requestObject: _requestObject,
            pageValue: _pageValue,
        }
        return requestValue;
    }

    createHeader(isRequireToken: Boolean): Observable<any> {
        return new Observable(obs => {
            const httpOptions = {
                headers: new HttpHeaders({
                    'Content-Type': 'application/json'
                })
            };
            if (isRequireToken) {
                this.tokenManagementService.getFullToken().subscribe(res => {
                    if (res) {
                        const httpOptions = {
                            headers: new HttpHeaders({
                                'Content-Type': 'application/json',
                                'Authorization': res + ''
                            })
                        };
                        obs.next(httpOptions)
                    }
                });
            } else {
                obs.next(httpOptions)
            }
        });
    }

    createRequestObject(request: any, pageValue: PageValue) {
        // var requestModel = new RequestObjectModel()
        // requestModel.requestObject = request
        // requestModel.pageValue = pageValue
        let requestValue = {
            requestObject: request,
            pageValue: pageValue,
        }
        return requestValue
    }

    createRequest(request: any) {
        let requestValue = {
            requestObject: [request]
        }
        return requestValue;
    }

    createRequestNotList(request: any) {
        let requestValue = {
            requestObject: request
        }
        return requestValue;
    }

    public managePost(url: string, requestValue): Observable<any> {
        return new Observable(obs => {
            const httpOptions = {
                headers: new HttpHeaders({
                    'Content-Type': 'application/json',
                    'Authorization': 'bearer eyJhbGciOiJIUzI1NiIsInR5cCI6IkpXVCJ9.eyJ0b2tlbiI6IjRhMDI0ZDU3LTRjYmItMmNhMy1hODljLWQ2ZDlkNGYyMDZkOSIsImV4cGlyZSI6MTU0ODY1MjYxNTQ4NiwiYXR0cnMiOnsidXNlcklkIjowfSwiaWF0IjoxNTQ4NjQ5MDE1fQ.rN3IDmE7_G3sWyriY2Egp7ESpRaXAOApak86aMzWW9A'
                })
            };
            this.tokenManagementService.getFullToken().subscribe(res => {
                if (res) {
                    const httpOptions = {
                        headers: new HttpHeaders({
                            'Content-Type': 'application/json',
                            'Authorization': res + ''
                        })
                    };
                    obs.next(this.http.post(url, requestValue, httpOptions))
                }
            })
        })
    }

}

