import { Injectable } from "@angular/core";
import { HttpClient } from "@angular/common/http";
import { Observable } from "rxjs";
import { HttpHeaders } from "@angular/common/http";
import { environment } from "../../../environments/environment";
import { HttpClientService } from "./httpClient/httpClient.service";

const httpOptions = {
    headers: new HttpHeaders({
        "Content-Type": "application/json"
        //'Authorization': 'my-auth-token'
    })
};

@Injectable()
export class ContentService {
    url = environment.SERVICE_URL_AUTH
    urlExplore = environment.SERVICE_URL_EXPLORE


    constructor(
        private http: HttpClient,
        private httpClientUtil: HttpClientService,
    ) { }

    // Service :: Get content by id
    public getContents(isRequireToken: Boolean, id: string, lang: string, type: string): Observable<any> {
        return new Observable(obs => {
            this.httpClientUtil.createHeader(isRequireToken).subscribe(header => {
                this.http.get(this.urlExplore + environment.API_CONTENT + "?id=" + id + "&lang=" + lang + "&type=" + type, header).subscribe(response => {
                    obs.next(response)
                }, err => {
                    obs.error(err)
                })

            })
        })
    }

}
