
import { Injectable } from "@angular/core";
import { HttpClient, HttpResponse } from "@angular/common/http";
import { Observable, throwError } from "rxjs";
import { AuthModel } from "../models/auth.model";
import { HttpHeaders, HttpErrorResponse } from "@angular/common/http";

import { UserLoginModel } from './../models/user-login.model';
import { environment } from "../../../environments/environment";

const httpOptions = {
	headers: new HttpHeaders({
		"Content-Type": "application/json"
		//'Authorization': 'my-auth-token'
	})
};

@Injectable()
export class LoginService {
	url = environment.SERVICE_URL_AUTH
	urlExplore = environment.SERVICE_URL_EXPLORE


	constructor(
		private http: HttpClient,

	) {}

	authManual(userProfile: UserLoginModel): Observable<AuthModel> {
		return this.http.post<AuthModel>(
			this.url + environment.API_AUTH_ENDPOINT,
			userProfile,
			httpOptions
		);
	}
	refreshToken(authModel: AuthModel): Observable<AuthModel> {
		return this.http.post<AuthModel>(
			this.url + environment.API_AUTH_ENDPOINT,
			authModel,
			httpOptions
		);
	}

	loginWeb(token: string): Observable<any> {
		httpOptions.headers = httpOptions.headers.set('Authorization', 'Bearer '+token)
		return this.http.get<any>(this.urlExplore+environment.API_LOGIN_WEB ,httpOptions)
	}

}
