import { BrowserModule } from '@angular/platform-browser';
import { NgModule } from '@angular/core';
import { RouterModule, Routes } from '@angular/router';

import { AppRoutingModule } from './app-routing.module';
import { AppComponent } from './app.component';
import { SingleDetailComponent } from './views/single-detail/single-detail.component';
import { MultipleDetailComponent } from './views/multiple-detail/multiple-detail.component';
import { TripDetailComponent } from './views/trip-detail/trip-detail.component';
import { PromotionDetailComponent } from './views/promotion-detail/promotion-detail.component';
import { BrowserAnimationsModule } from '@angular/platform-browser/animations';
import { HttpClientModule } from '@angular/common/http';
import { CoreModule } from './core/core.module';
import { MatTabsModule } from '@angular/material/tabs';
import { MatSelectModule } from '@angular/material/select';

const routes: Routes = [
  {
    path: 'content/:id/:lang',
    component: SingleDetailComponent
  },
  {
    path: 'story/:id/:lang',
    component: MultipleDetailComponent
  },
  {
    path: 'trip',
    component: TripDetailComponent
  },
  {
    path: 'promotion',
    component: PromotionDetailComponent
  },
];

@NgModule({
  declarations: [
    AppComponent,
    SingleDetailComponent,
    MultipleDetailComponent,
    TripDetailComponent,
    PromotionDetailComponent
  ],
  imports: [
    BrowserModule,
    AppRoutingModule,
    MatTabsModule,
    MatSelectModule,
    RouterModule.forChild(routes),

    BrowserAnimationsModule,
    HttpClientModule,
    CoreModule,
  ],
  providers: [],
  bootstrap: [AppComponent]
})
export class AppModule { }
