import { Injectable } from "@angular/core";
import { Observable } from "rxjs";
import { LoginService } from "../services/login.service";
import { } from "rxjs/operators";
import { AuthModel } from "../models/auth.model";
import { Router } from '@angular/router';

@Injectable()
export class TokenManagementService {
	constructor(
		private loginService: LoginService,
		private router: Router) { }

	isLogin(): Boolean {
		let currentUser = JSON.parse(localStorage.getItem("currentUser"));
		let oldExpireTime = localStorage.getItem("oldExpireTime");
		if (currentUser && oldExpireTime) {
			return true;
		}
		return false;
	}

	isValidSession(): Observable<Boolean> {
		return new Observable(obs => {
			let currentUser = JSON.parse(localStorage.getItem("currentUser"));
			let oldExpireTime = localStorage.getItem("oldExpireTime");

			if (currentUser && oldExpireTime) {
				var now = new Date();
				var expireTime = new Date(oldExpireTime);
				if (now < expireTime) {
					obs.next(true);
				} else {
					obs.next(false);
				}
			} else {
				obs.next(false);
			}
		});
	}

	updateSession(): Observable<AuthModel> {
		return new Observable(obs => {
			let currentUser = JSON.parse(localStorage.getItem("currentUser"));
			let oldExpireTime = localStorage.getItem("oldExpireTime");
			var authModel = new AuthModel();
			authModel.refresh_token = currentUser.refresh_token;

			if (currentUser && oldExpireTime) {
				this.loginService.refreshToken(authModel).subscribe(
					response => {
						// Refresh success, then update localStorage
						// console.log("RefreshToken response => ", response);
						this.updateTokenData(response);
						obs.next(response);
					},
					err => {
						console.log(
							"Refresh token failed. Please back to Login Page."
						);
						this.clearTokenData()
						this.router.navigate(['/auth/login'])

						obs.next(null);
					}
				);
			}
		});
	}

	updateTokenData(authModel: AuthModel) {
		localStorage.setItem("currentUser", JSON.stringify(authModel));
		let currentUser = JSON.parse(localStorage.getItem("currentUser"));

		var date = new Date();
		date.setSeconds(date.getSeconds() + currentUser.expires_in);
		localStorage.setItem("oldExpireTime", date + "");
	}

	clearTokenData() {
		// localStorage.removeItem("refreshToken");
		localStorage.removeItem("currentUser");
		localStorage.removeItem("oldExpireTime");
		// this.setAccessToken(null);
		// this.setTokenType(null);
	}

	getFullToken(): Observable<String> {
		return new Observable(obs => {
			let currentUser = JSON.parse(localStorage.getItem("currentUser"));
			let oldExpireTime = localStorage.getItem("oldExpireTime");

			// if (currentUser) {
			// 	// console.log(currentUser.access_token);
			// 	obs.next(
			// 		currentUser.token_type + " " + currentUser.access_token
			// 	);
			// }

			if (currentUser && oldExpireTime) {
				var now = new Date();
				var expireTime = new Date(oldExpireTime);
				if (now < expireTime) {
					obs.next(currentUser.token_type + " " + currentUser.access_token);
				} else {
					// Timeout, request new token
					this.updateSession().subscribe(
						authModel => {
							obs.next(authModel.token_type + " " + authModel.access_token);
						},
						err => {
							console.log(err);
							obs.next(null);
						}
					);
				}
			} else {
				obs.next(null);
			}
		});
	}
}
